.diagram {
	position: relative;
	margin: 0 auto 80px;
	width: 1220px;
	height: 620px;
	@include xl {
		margin-bottom: 50px;
		width: 720px;
		height: 1130px; }
	@include sm {
		overflow: hidden;
		margin-bottom: 0;
		max-width: 320px;
		height: 1216px; }
	&__in,
	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0; }
	&__in {
		@include sm {
			left: 52px;
			right: 29px;
			bottom: 86px; } }
	&__overlay {
		z-index: 5;
		opacity: 0;
		visibility: hidden;
		background: rgba($black, 0.5);
		transition: opacity 0.2s ease, visibility 0.2s;
		@include sm {
			left: -52px;
			right: -29px; }
		&.is-open {
			opacity: 1;
			visibility: visible; } }
	//footer
	&__logo {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		padding: 16px 0 0;
		font-size: 0;
		text-align: center;
		@include xl {
			padding-top: 26px; }
		@include sm {
			top: auto;
			bottom: 0;
			padding: 0; }
		&:after {
			content: '';
			display: block;
			margin: 0 auto;
			border-top: 3px solid #1A1A1A;
			max-width: 1120px; }
		img {
			margin-bottom: 16px;
			width: 143px;
			@include xl {
				margin-bottom: 19px; }
			@include sm {
				margin-bottom: 16px; } } }
	//bg
	&__bg {
		position: absolute;
		top: 60px;
		left: 50px;
		right: 50px;
		bottom: 0;
