				top: 24px; }
			&[data-diagram="hint-item-2"] {
				top: 126px; }
			&[data-diagram="hint-item-3"] {
				top: 173px; }
			&[data-diagram="hint-item-4"] {
				top: 31px;
				padding: 10px 0; }
			&[data-diagram="hint-item-1"],
			&[data-diagram="hint-item-2"],
			&[data-diagram="hint-item-3"],
			&[data-diagram="hint-item-4"] {
				@include xl {
					position: relative;
					top: auto;
					left: auto;
					margin-bottom: 10px; } }
			small {
				display: block;
				font-size: 11px; } } }
	[data-diagram-modal="parent"] {
		cursor: pointer;
		transition: color 0.2s ease, background 0.2s;
		&.is-active {
			z-index: 10;
			&:not([data-diagram-hover="no"]) {
				color: $black;
				background: $white; }
			[data-diagram-modal="child"] {
				opacity: 1;
				visibility: visible; } } }
	&__modal {
		position: absolute;
		z-index: 10;
		top: 100%;
		left: 100%;
		padding: 15px 20px 18px;
		//margin: -21px 0 0 -64px
		border-radius: 10px;
		width: 400px;
		opacity: 0;
		visibility: hidden;
		color: $white;
		background: #30363D;
		@include f(13, 19);
		text-align: left;
		pointer-events: none;
		transition: opacity 0.2s ease, visibility 0.2s;
		@include xl {
			display: none; }
		p {
			margin-bottom: 5px; }
		ul {
			list-style-type: disc;
			list-style-position: inside; } }
	&__formula {
		margin: 15px auto 0;
		max-width: 180px; } }
