.modal {
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: auto;
	padding: 15px;
	opacity: 0;
	visibility: hidden;
	background: rgba($black, 0.3);
	@include vertical;
	transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
	@include xs {
		padding: 0; }
	&.is-open {
		opacity: 1;
		visibility: visible; }
	&__container {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		border-radius: 30px;
		width: 100%;
		max-width: 970px;
		color: $black;
		background: $white;
		@include f(16, 24);
		@include sm {
			@include f(14, 22); }
		@include xs {
			border-radius: 0; } }
	&__close {
		position: absolute;
		z-index: 10;
		top: 20px;
		right: 20px;
		padding: 10px;
		color: $black;
		opacity: 0.2;
		background: none;
		transition: opacity 0.17s linear;
		@include hover {
			opacity: 0.5; }
		@include sm {
			top: 8px;
			right: 8px; }
		.icon {
			font-size: 24px;
			@include sm {
				font-size: 16px; } } } }
