.faq {
	position: relative;
	padding: 164px 0 38px;
	@include md {
		padding-bottom: 65px; }
	@include sm {
		padding-bottom: 75px; }
	&__inner {
		margin: 0 auto;
		max-width: 570px;
		@include md {
			max-width: 595px; } }
	&__logo {
		display: block;
		margin: 0 auto 23px;
		width: 48px;
		@include md {
			margin-bottom: 26px; }
		@include sm {
			margin-bottom: 34px; } }
	&__title {
		margin-bottom: 83px;
		text-align: center;
		@include sm {
			margin-bottom: 40px; } }
	&__section {
		&:not(:first-child) {
			padding-top: 75px;
			@include md {
				padding-top: 60px; }
			@include sm {
				padding-top: 45px; } } }
	&__subtitle {
		margin-bottom: 22px;
		color: $lightGray;
		@include f(18, 24, null, 700);
		letter-spacing: 0.18em;
		text-transform: uppercase;
		@include sm {
			margin-bottom: 17px; } }
	&__box {
		padding: 25px 0;
		@include sm {
			padding: 15px 0; }
		p {
			@include f(16, 24); }
		a {
			color: $green; }
		ul {
			margin-top: 24px;
			@include f(16, 24);
			list-style-type: disc;
			list-style-position: inside; } }
	&__label {
		padding-bottom: 10px;
		color: $blue;
		@include f(12, 24, null, 700);
		letter-spacing: 0.2em;
		text-transform: uppercase; } }
